.tb_tag_filter_container{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.tb_tag_filter_list{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 6px;
    position: relative;
}

.tb_tag_btn{
    font-family: inherit;
    padding: 0 12px;
    width: auto;
    height: 38px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #34495e;
    border: 1px solid #34495e;
    text-decoration: none;
    line-height: 38px;
    font-weight: 600;
    font-size: 13px;
    text-align: center;
    background-color: #fff;
    transition: all .3s ease;
    -webkit-transition: all .3s ease;
    cursor: pointer;
    position: relative;
    z-index: 2;
    width: auto;
    border-radius: 5px;
    min-width: 60px;
}

.tb_tag_btn:hover,
.tb_tag_btn:focus,
.tb_tag_btn.tb_active{
    color: #fff;
    background-color: #34495e;
}

.tb_tag_btn_dropdown{
    top: 120%;
    position: absolute;
    left: auto;
    right: 6px;
    height: auto;
    background-color: #fff;
    width: 300px;
    display: flex;
    align-items: center;
    justify-content: end;
    flex-wrap: wrap;
    padding: 12px;
    z-index: 99;
    opacity: 0;
    visibility: hidden;
    border-radius: 4px;
    box-shadow: 0px 8px 18px rgba(0, 0, 0, 0.16);
    transition: all .2s ease;
    -webkit-transition: all .2s ease;
}

.tb_tag_btn_dropdown .tb_tag_filter_list{
    padding: 4px;
}

.tb_tag_btn_dropdown .tb_tag_btn{
    font-size: 12px;
    padding: 0 10px;
    height: 32px;
}

.tb_tag_filter_list:hover .tb_tag_btn_dropdown{
    visibility: visible;
    opacity: 1;
    top: 100%;
}

.tb_tag_btn_arrow {
    margin-left: 5px;
    font-size: 10px;
}

@media screen and (max-width: 480px) {
.tb_tag_btn_dropdown{
    width: 250px;
}
}