.tb_filter_rectangular_wrapper{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    position: relative;
}

.tb_filter_rectangular_list{
    padding: 6px;
    position: relative;
}

.tb_filter_rectangular_button {
    height: 38px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    text-align: center;
    background-color: #fff;
    color: #34495e;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
    cursor: pointer;
    position: relative;
    z-index: 2;
    width: auto;
    border-radius: 5px;
    padding: 8px 10px;
    box-shadow: 0 2px 4px rgba(0,0,0,.3);
}

.tb_filter_rectangular_icon {
    font-size: 18px;
    line-height: 22px;
    padding-right: 8px;
    position: relative;
}
.tb_filter_rectangular_ico, .tb_colr_ico:before{
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
}
.tb_filter_rectangular_icon:after {
    content: "";
    display: block;
    width: 1px;
    height: 100%;
    top: 0;
    right: 0;
    left: auto;
    z-index: 1;
    background-color: #ff4051;
    position: absolute;
    opacity: .5;
    transition: all .3s ease;
    -webkit-transition: all .3s ease;
}

.tb_filter_rectangular_button_text {
    font-family: inherit;
    padding-left: 8px;
    font-size: 16px;
    font-weight: 400;
    line-height: normal;
}

.tb_filter_rectangular_button.tb_filter_active, 
.tb_filter_rectangular_button:hover, 
.tb_filter_rectangular_button:focus{
    background-color: #34495e;
    color: #fff;
}

.tb_filter_rectangular_button:active .tb_filter_rectangular_icon,
.tb_filter_rectangular_button.tb_filter_active .tb_filter_rectangular_icon, 
.tb_filter_rectangular_button:hover .tb_filter_rectangular_icon, 
.tb_filter_rectangular_button:focus .tb_filter_rectangular_icon{
    color: #fff;
}

